@media only screen and (max-width: 768px) {
    .contact-me{ 
        background-image: url("../assets/banner-bg-mob.png") !important;   
    }
    .details {
        display: block !important;
        text-align: center !important;
        width: 100% !important;
        padding: 0rem !important;
        color: white !important;
        font-size: 1.2rem !important;
    }
    .greeting {
        max-width: none !important;
    }

    .contact-details {
        max-width: 100% !important;
        font-size: 1rem !important;
    }

    .title {
        font-size: 4rem !important;
        font-weight: 600 !important;
        color: white !important;
    }

    .container {
        padding: 1rem !important;
        text-align: center;
        mix-blend-mode: difference;
    }

    .location {
        color: white !important;
        border-color: white !important;
        margin: .6rem 0rem;
        font-size: 1rem !important;
    }

    .submit button {
        width: 100% !important;
    }

  }
.contact-me{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    position: relative;
    color: white;
    overflow-x: hidden;
    background-image: url("../assets/banner-bg.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
}

.container {
    padding-bottom: 4rem;
}

.title {
    font-size: 9rem;
    letter-spacing: -4px;
    font-weight: 800;
    margin-bottom: 2rem;
    color: #f5f5f5;
}

.details {
    display: flex;
    justify-content: space-between;
    width: 800px;
    font-size: 1.5rem;
    color:#aaa;
    padding: 0rem 2rem
}

.location {
    display: block;
    cursor: pointer;
    text-decoration: none;
    color: #aaa;
    text-align: center;
    border: 2px solid #aaa;
    padding: .3rem;
    margin-top: 1.5rem;
    font-size: 1.3rem;
    transition: background-color .3s, color .3s;
}

.location:hover {
    background-color: #aaa;
    color: white;

}

.greeting {
    max-width: 400px;
    line-height: 1.6;
}

.contact-details {
    max-width: 180px;
    font-size: 1.1rem;
}

.contact-details div{
    padding: .3rem 0rem;
}

.email {
    cursor: pointer;
}

.contact-me-form {
    text-align: center;
    margin: auto;
    margin-top: 8rem;
    margin-bottom: 4rem;
    max-width: 1000px;
    padding: 0rem 1rem;
}

.form-container {
    margin-top: 2.5rem;
    padding: 2rem 0rem;
}

.subtitle { 
    margin-top: 2rem;
    font-size: 1.2rem;
}

.form-input input, .form-input textarea{
    display: block;
    width: 100%;
    padding: .75rem;
    border: 1px solid #999;
    transition: box-shadow .3s;
}

.form-input input:focus-visible, .form-input textarea:focus-visible{
    outline: none !important;
    box-shadow: 0px 0px 4px #aaa;
}

textarea { 
    min-height: 160px;
    width: 100% !important;
}

.form-row {
    display: flex;
    justify-content: space-between;
}

.form-input {
    margin-bottom: 2rem;
    text-align: left;
}

.form-input.half{
    width: 48% !important;
    display: inline-block;
}

.form-input.half:nth-child(1){
    text-align: left;
    margin-right: auto;
}

.submit {
    display: block;
    text-align: left;
}

.submit button {
    background-color: transparent;
    border: 2px solid black;
    padding: .5rem;
    width: 160px;
    font-size: 1.2rem;
    cursor: pointer;
    transition: background-color .3s, color .3s;
}

.submit button:hover {
    background-color: black;
    color: white;
}

input, textarea {
    box-sizing: border-box;
}

